<template>
    <div class="table_box">
        <TableCard title="告警信息管理" :table_data="table_data" :search_box_visibale="false" :table_item="table_item"
            :user="'warning_msg'" :add_btn_visible="false" @SizeChange="SizeChange" @currentPage="currentPage"
            @is_add_dialog_visible="is_add_dialog_visible" @row_id="row_id" @filter_first="filter_first" />

    </div>
</template>

<script>
import TableCard from '@/components/reusable_components/table_card.vue'
export default {
    components: {
        TableCard,
    },

    data() {
        return {
            table_item: [
                // { key: 'index', value: '序号', default: '' },
                { key: 'title', value: '标题', default: '' },
                { key: 'content', width: '250', value: '内容', default: '' },
                { key: 'sender', value: '发送者', default: '' },
                { key: 'send_time', value: '发送时间', default: '' },
                { key: 'status', value: '状态', default: '' },
            ],
            table_data: [],
            pageSize: 10,
            current_page: 1,
            add_dialog_visible: false,
            delete_row: 0,
            row_index: 0,
            // 新增

        }
    },

    mounted() {
        this.SizeChange(this.pageSize);
        this.currentPage(this.current_page);
    },

    methods: {

        // 根据子组件传来的信息，显示/关闭对话框
        is_add_dialog_visible(val) {
            this.add_dialog_visible = val
        },

        async filter_first(val) {
            let url = ''
            switch (val) {
                case '不限':
                    url = '/findAllWarning'
                    break
                case '待审批':
                    url = '/findAllWarningNeedCope'
                    break
                case '无需处理':
                    url = '/findAllWarningNoRequired'
                    break
                case '处理中':
                    url = '/findAllWarningCoping'
                    break
                case '处理完成':
                    url = '/findAllWarningDone'
                    break

                default:
                    break;
            }

            try {
                const res = await this.$http.post(url, {});
                if (res.data.code === 2000) {
                    this.table_data = res.data.data
                } else {
                    this.$message.error('出错啦')
                }
            } catch (error) {
                // console.log(error);
            }
        },

        async get_total_number() {
            try {
                const res = await this.$http.get("/findTotalNumberOfAttractions");
                if (res.data.code === 2000) {
                    this.total = res.data.data
                }
            } catch (error) {
                // console.log(error);
            }
        },

        async get_warnings() {
            try {
                const res = await this.$http.post('/findAllWarning', {
                    index: this.current_page,
                    pagesize: this.pageSize
                });
                // console.log(res)
                this.table_data = res.data.data
                this.get_total_number()
            } catch (error) {
            }
        },

        // 获取子组件传来的数据，当分页数据变化时，发送请求
        async SizeChange(size) {
            this.pageSize = size;
            this.get_warnings()
        },

        async currentPage(page) {
            this.current_page = page;
            this.get_warnings()
        },

        // 获取子组件传来的row_id
        row_id(val) {
            this.row_index = val
            this.add_form = this.table_data.find(item => item.id === val)
        },

        // async submit() {
        //     console.log('修改成功')
        //     try {
        //         const res = await this.$http.post("", {
        //             // 携带数据
        //         });
        //         // 成功后关闭窗口
        //         this.add_dialog_visible = false
        //     } catch (error) {
        //         // console.log(error);
        //     }
        // },

    },

}
</script>

<style lang="less" scoped>
.table_box {
    width: 100%;
    height: 100%;
}

.el-input {
    display: inline-block;
}
</style>