<script>
import SvgIcon from '@/components/reusable_components/SvgIcon.vue';
export default {
    components: {
        SvgIcon,
    },

    data() {
        return {
            user: 0,
            update_password_visible: false,
            circleUrl: "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
            username: '一寒止水',
            data: {
                id: 0,
                name: "黄山风景区",
                start_time: '2023-07-27',
                end_time: '2023-07-27',
                gathering_place: '景区东门',
                accommodation: '住宿安排',
                starting_point: '起点',
                destination: '终点',
                price: '2510',
                schedule: 'A->B->C->D->E',
                current_people: 20,
                guide_name: '一寒止水',
                agency: '蓝天旅行社',
                img: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
            },
        }
    },

    mounted() {
        this.get_guide_itinerary()
    },

    methods: {
        // 返回上一页
        goBack() {
            sessionStorage.index = 1
            this.$router.push('/user')
        },

        async get_guide_itinerary() {
            try {
                const res = await this.$http.post("/itinerary/findgitin", {
                    // 携带数据
                    id: sessionStorage.user_id
                });
                this.data = res.data.data
            } catch (error) { }
        },

        logout() { },
    }
}
</script>

