<template>
    <div class="table_box">
        <TableCard title="企业管理" :table_data="table_data" :table_item="table_item" :user="'administrator'"
            @SizeChange="SizeChange" @currentPage="currentPage" @is_add_dialog_visible="is_add_dialog_visible"
            @row_id="row_id" :total="total" />
        <el-dialog title="管理人员信息维护" :visible.sync="add_dialog_visible" center width="40%">
            <el-form :model="add_form" label-position="right" :rules="rules" ref="add_form_ref">
                <el-form-item label="企业">
                    <el-input v-model="add_form.affiliated_enterprise" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="add_form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="年龄">
                    <el-input v-model="add_form.age" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-input v-model="add_form.gender" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="职位">
                    <el-input v-model="add_form.position" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="add_dialog_visible = false" size="medium">取 消</el-button>
                <el-button type="primary" @click="submit" size="medium">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import TableCard from '@/components/reusable_components/table_card.vue'
import { rules } from '@/utils/rules.js'
export default {
    components: {
        TableCard,
    },

    data() {
        return {
            table_item: [
                // { key: 'index', value: '序号', default: '' },
                { key: 'affiliated_enterprise', value: '企业', default: '' },
                { key: 'name', value: '姓名', default: '' },
                { key: 'age', value: '年龄', default: '' },
                { key: 'gender', value: '性别', default: '' },
                { key: 'position', value: '职位', default: '' },
            ],
            table_data: [],
            pageSize: 10,
            current_page: 1,
            add_dialog_visible: false,
            delete_row: 0,
            row_index: 0,
            total: 0,
            // 新增
            add_form: {
                id: 0,
                affiliated_enterprise: '',
                name: 0,
                age: 0,
                gender: '男',
                position: '员工',
            },

            rules: null,

        }
    },

    mounted() {
        this.SizeChange(this.pageSize);
        this.currentPage(this.current_page);
        // this.rules = rules
    },

    created() {
        this.rules = rules

    },

    methods: {

        // 根据子组件传来的信息，显示/关闭对话框
        is_add_dialog_visible(val) {
            this.add_dialog_visible = val
        },

        async get_total_number() {
            try {
                const res = await this.$http.get("/findAdministratorTotalNumber");
                if (res.data.code === 2000) {
                    this.total = res.data.data
                }
            } catch (error) {
                // console.log(error);
            }
        },


        async get_admins() {
            try {
                const res = await this.$http.post('/findAllAdministrator', {
                    index: this.current_page,
                    pagesize: this.pageSize
                });
                // console.log(res)
                this.table_data = res.data.data
                this.get_total_number()
            } catch (error) {
            }
        },

        // 获取子组件传来的数据，当分页数据变化时，发送请求
        async SizeChange(size) {
            this.pageSize = size;
            this.get_admins()
        },

        async currentPage(page) {
            this.current_page = page;
            this.get_admins()
        },

        // 获取子组件传来的row_id
        row_id(val) {
            this.row_index = val
            this.add_form = this.table_data.find(item => item.id === val)
        },

        async submit() {
            try {
                const res = await this.$http.put("/updateTourismEnterprise", {
                    // 携带数据
                    id: this.row_index + '',
                    name: this.add_form.name,
                    affiliated_enterprise: this.add_form.affiliated_enterprise,
                    gender: this.add_form.gender,
                    position: this.add_form.position,
                    age: this.add_form.age
                });
                // 成功后关闭窗口
                if (res.data.code === 2000) {
                    console.log(res.data.data)
                } else if (res.data.code === 4000) {
                    this.$message.error('修改失败！');
                } else {
                    this.$message.error('出现错误啦！')
                }
                this.add_dialog_visible = false
            } catch (error) {
                // console.log(error);
            }
        },

    },

}
</script>

<style lang="less" scoped>
.table_box {
    width: 100%;
    height: 100%;
}

.el-input {
    display: inline-block;
}
</style>