<template>
    <div>
        <h1>导游人员管理条例</h1>
        <div v-html="need_to_know">
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            need_to_know: '<b>第一条</b>　为了规范导游活动，保障旅游者和导游人员的合法权益，促进旅游业的健康发展，制定本条</b>例。'
                + '<br><b>第二条</b>　本条</b>例所称导游人员，是指依照本条</b>例的规定取得导游证，接受旅行社委派，为旅游者提供向导、讲解及相关旅游服务的人员。'
                + '<br><b>第三条</b>　国家实行全国统一的导游人员资格考试制度。'
                + '<br>具有高级中学、中等专业学校或者以上学历，身体健康，具有适应导游需要的基本知识和语言表达能力的中华人民共和国公民，可以参加导游人员资格考试；经考试合格的，由国务院旅游行政部门或者国务院旅游行政部门委托省、自治区、直辖市人民政府旅游行政部门颁发导游人员资格证书。'
                + '<br><b>第四条</b>　在中华人民共和国境内从事导游活动，必须取得导游证。'
                + '<br>取得导游人员资格证书的，经与旅行社订立劳动合同或者在相关旅游行业组织注册，方可持所订立的劳动合同或者登记证明材料，向省、自治区、直辖市人民政府旅游行政部门申请领取导游证。'
                + '<br>导游证的样式规格，由国务院旅游行政部门规定。'
                + '<br><b>第五条</b>　有下列情形之一的，不得颁发导游证：'
                + '<br>（一）无民事行为能力或者限制民事行为能力的；'
                + '<br>（二）患有传染性疾病的；'
                + '<br>（三）受过刑事处罚的，过失犯罪的除外；'
                + '<br>（四）被吊销导游证的。'
                + '<br><b>第六条</b>　省、自治区、直辖市人民政府旅游行政部门应当自收到申请领取导游证之日起15日内，颁发导游证；发现有本条</b>例<b>第五条</b>规定情形，不予颁发导游证的，应当书面通知申请人。'
                + '<br><b>第七条</b>　导游人员应当不断提高自身业务素质和职业技能。'
                + '<br>国家对导游人员实行等级考核制度。导游人员等级考核标准和考核办法，由国务院旅游行政部门制定。'
                + '<br><b>第八条</b>　导游人员进行导游活动时，应当佩戴导游证。'
                + '<br>导游证的有效期限为3年。导游证持有人需要在有效期满后继续从事导游活动的，应当在有效期限届满3个月前，向省、自治区、直辖市人民政府旅游行政部门申请办理换发导游证手续。'
                + '<br><b>第九条</b>　导游人员进行导游活动，必须经旅行社委派。'
                + '<br>导游人员不得私自承揽或者以其他任何方式直接承揽导游业务，进行导游活动。'
                + '<br><b>第十条</b>　导游人员进行导游活动时，其人格尊严应当受到尊重，其人身安全不受侵犯。'
                + '<br>导游人员有权拒绝旅游者提出的侮辱其人格尊严或者违反其职业道德的不合理要求。'
                + '<br><b>第十一条</b>　导游人员进行导游活动时，应当自觉维护国家利益和民族尊严，不得有损害国家利益和民族尊严的言行。'
                + '<br><b>第十二条</b>　导游人员进行导游活动时，应当遵守职业道德，着装整洁，礼貌待人，尊重旅游者的宗教信仰、民族风俗和生活习惯。'
                + '<br>导游人员进行导游活动时，应当向旅游者讲解旅游地点的人文和自然情况，介绍风土人情和习俗；但是，不得迎合个别旅游者的低级趣味，在讲解、介绍中掺杂庸俗下流的内容。'
                + '<br><b>第十三条</b>　导游人员应当严格按照旅行社确定的接待计划，安排旅游者的旅行、游览活动，不得擅自增加、减少旅游项目或者中止导游活动。'
                + '<br>导游人员在引导旅游者旅行、游览过程中，遇有可能危及旅游者人身安全的紧急情形时，经征得多数旅游者的同意，可以调整或者变更接待计划，但是应当立即报告旅行社。'
                + '<br><b>第十四条</b>　导游人员在引导旅游者旅行、游览过程中，应当就可能发生危及旅游者人身、财物安全的情况，向旅游者作出真实说明和明确警示，并按照旅行社的要求采取防止危害发生的措施。'
                + '<br><b>第十五条</b>　导游人员进行导游活动，不得向旅游者兜售物品或者购买旅游者的物品，不得以明示或者暗示的方式向旅游者索要小费。'
                + '<br><b>第十六条</b>　导游人员进行导游活动，不得欺骗、胁迫旅游者消费或者与经营者串通欺骗、胁迫旅游者消费。'
                + '<br><b>第十七条</b>　旅游者对导游人员违反本条</b>例规定的行为，有权向旅游行政部门投诉。'
                + '<br><b>第十八条</b>　无导游证进行导游活动的，由旅游行政部门责令改正并予以公告，处1000元以上3万元以下的罚款；有违法所得的，并处没收违法所得。'
                + '<br><b>第十九条</b>　导游人员未经旅行社委派，私自承揽或者以其他任何方式直接承揽导游业务，进行导游活动的，由旅游行政部门责令改正，处1000元以上3万元以下的罚款；有违法所得的，并处没收违法所得；情节严重的，由省、自治区、直辖市人民政府旅游行政部门吊销导游证并予以公告。'
                + '<br><b>第二十条</b>　导游人员进行导游活动时，有损害国家利益和民族尊严的言行的，由旅游行政部门责令改正；情节严重的，由省、自治区、直辖市人民政府旅游行政部门吊销导游证并予以公告；对该导游人员所在的旅行社给予警告直至责令停业整顿。'
                + '<br><b>第二十一条</b>　导游人员进行导游活动时未佩戴导游证的，由旅游行政部门责令改正；拒不改正的，处500元以下的罚款。'
                + '<br><b>第二十二条</b>　导游人员有下列情形之一的，由旅游行政部门责令改正，暂扣导游证3至6个月；情节严重的，由省、自治区、直辖市人民政府旅游行政部门吊销导游证并予以公告：'
                + '<br>（一）擅自增加或者减少旅游项目的；'
                + '<br>（二）擅自变更接待计划的；'
                + '<br>（三）擅自中止导游活动的。'
                + '<br><b>第二十三条</b>　导游人员进行导游活动，向旅游者兜售物品或者购买旅游者的物品的，或者以明示或者暗示的方式向旅游者索要小费的，由旅游行政部门责令改正，处1000元以上3万元以下的罚款；有违法所得的，并处没收违法所得；情节严重的，由省、自治区、直辖市人民政府旅游行政部门吊销导游证并予以公告；对委派该导游人员的旅行社给予警告直至责令停业整顿。'
                + '<br><b>第二十四条</b>　导游人员进行导游活动，欺骗、胁迫旅游者消费或者与经营者串通欺骗、胁迫旅游者消费的，由旅游行政部门责令改正，处1000元以上3万元以下的罚款；有违法所得的，并处没收违法所得；情节严重的，由省、自治区、直辖市人民政府旅游行政部门吊销导游证并予以公告；对委派该导游人员的旅行社给予警告直至责令停业整顿；构成犯罪的，依法追究刑事责任。'
                + '<br><b>第二十五条</b>　旅游行政部门工作人员玩忽职守、滥用职权、徇私舞弊，构成犯罪的，依法追究刑事责任；尚不构成犯罪的，依法给予行政处分。'
                + '<br><b>第二十六条</b>　景点景区的导游人员管理办法，由省、自治区、直辖市人民政府参照本条</b>例制定。'
                + '<br><b>第二十七条</b>　本条</b>例自1999年10月1日起施行。1987年11月14日国务院批准、1987年12月1日国家旅游局发布的《导游人员管理暂行规定》同时废止。',
        }
    }
}
</script>

