import register from "@/components/index/register";

// 表单验证规则
let comfirm_password_validator = (rule, value, callback) => {
  if (value === "") {
    callback(new Error("请再次输入密码"));
  } else if (value !== register.REGISTER.password) {
    callback(new Error("两次输入密码不一致!"));
  } else {
    callback();
  }
};
